import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    ifDefined,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { buttonStyles } from './buttonStyle';
import { ButtonType } from './buttonType';
import { ButtonSize } from './buttonSize';
import { ButtonDesign, ButtonProperties, ButtonSlot } from './buttonProperties';
import { version } from './metadata';

@defineElement({ tagName: 'button', styles: buttonStyles, version })
export class Button extends BaseElement implements ButtonProperties {
    // Static members
    private static Loader;
    private static Spinner;
    // Private members

    // Protected members

    // Public members
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    active = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    disabled = false;
    @property({ type: Boolean, reflect: true, attribute: 'full-width', converter: booleanConverter })
    fullWidth = false;
    @property({ type: Boolean, reflect: true, attribute: 'full-height', converter: booleanConverter })
    fullHeight = false;
    @property({ type: ButtonType })
    type = ButtonType.Primary;
    @property({ type: ButtonDesign })
    design = ButtonDesign.Default;
    @property({ type: Boolean, converter: booleanConverter })
    compact = false;
    @property({ type: ButtonSize, attribute: 'button-size' })
    buttonSize = ButtonSize.Default;
    @property({ type: String, attribute: 'button-type' })
    buttonType = 'button';
    @property({ type: Object, attribute: false })
    form;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'unseen-notifications' })
    unseenNotification = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'icon-after' })
    iconAfter = false;
    @property({ type: Boolean, converter: booleanConverter })
    get loaded() {
        return this._loaded;
    }

    set loaded(ready) {
        if (ready && this._loadingTimerHandle) {
            clearTimeout(this._loadingTimerHandle);
            delete this._loadingTimerHandle;
        }
        if (this._loaded !== ready) {
            if (ready) {
                this._loaded = ready;
                this.requestUpdate();
            } else {
                this._loadingTimerHandle = setTimeout(() => {
                    // Avoid loader being stuck in loading state when loaded is set from false to true before 400ms
                    if (!ready && this.loaded && this._loadingTimerHandle === undefined) {
                        return;
                    }

                    delete this._loadingTimerHandle;
                    this._loaded = ready;
                    this.requestUpdate();
                }, 400);
            }
        }
    }

    @state()
    private _hasIcon;
    @state()
    private _loaderShown;
    @query('button')
    private _button;
    @query('[class$="-inner"]')
    private _inner;
    private _spinnerVariant = 'light-grey';
    private _loaded = true;
    private _loadingTimerHandle?: any;
    private _showLoaderTimerHandle?: any;

    // Public methods

    constructor() {
        super();
        this.addEventListener('click', this._onClick);
    }

    connectedCallback() {
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-loader'), elem: ['Loader'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-spinner'), elem: ['Spinner'] });
        Button.dynSubRegister(imports, () => {
            this.addEventListener('loader-start', this._handleLoaderStart);
            this.addEventListener('loader-end', this._handleLoaderEnd);
            super.connectedCallback();
        });
    }

    public focus() {
        this._button.focus();
    }

    public blur() {
        this._button.blur();
    }

    protected updated(changes) {
        if (changes.has('active') && changes.get('active') && !this.active) {
            this.dispatchEvent(
                new CustomEvent('button-active-toggle', {
                    bubbles: true,
                    composed: true,
                    detail: { active: this.active },
                })
            );
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            'full-width': this.fullWidth,
            'full-height': this.fullHeight,
            [this.type]: true,
            disabled: this._disabled(),
            active: this.active,
            compact: this.compact,
            [this.buttonSize]: true,
            [this.design]: !!this.design,
            'unseen-notifications': this.unseenNotification,
            responsive: this._hasIcon,
            'icon-after': this.iconAfter,
        });

        const loaderHtml = this._renderButtonLoader(baseClassName);

        return html`
            <button
                class="${rootClasses}"
                ?disabled="${this._disabled()}"
                ?active="${this.active}"
                type="${ifDefined(this.buttonType ? this.buttonType : undefined)}"
                button-size="${this.buttonSize}"
            >
                <div class="${baseClassName}-inner">${loaderHtml}</div>
            </button>
        `;
    }

    private _renderButtonLoader(baseClassName) {
        this._setSpinnerVariant();
        const labelSlot = html`<span class="${baseClassName}-label"><slot></slot></span>`;
        const buttonPadding = this.loaded && !this._loaderShown ? 'unset' : this._getButtonWidth() + 'px';
        if (this.design === ButtonDesign.Notification) {
            return html`<div class="${baseClassName}-circle"><slot name="${ButtonSlot.NUMBER}"></slot></div>
                ${labelSlot}`;
        } else if (this._hasIcon) {
            return html`<${Button.Loader.tag} toggleable class="${baseClassName}-loader" loaded="${this.loaded}">
                <div class="${baseClassName}-loader-icon" slot="loader-slot"><${Button.Spinner.tag} variant="${
                    this._spinnerVariant
                }" size="16px"></${Button.Spinner.tag}></div>
                    ${this._renderIconSlot()}
                </${Button.Loader.tag}> ${labelSlot}`;
        } else {
            return html`
                    <${Button.Loader.tag} toggleable class="${baseClassName}-loader" loaded="${this.loaded}" style="width: ${buttonPadding}">
                        <${Button.Spinner.tag} variant="${this._spinnerVariant}" size="16px" slot="loader-slot">
                        </${Button.Spinner.tag}>${this._renderIconSlot()} ${labelSlot}
                    </${Button.Loader.tag}>
                `;
        }
    }

    private _renderIconSlot = () => html`<slot name="${ButtonSlot.ICON}" @slotchange=${this._onIconSlotChange}></slot>`;

    private _onIconSlotChange = (e) => {
        const target = e.target as HTMLSlotElement;
        this._hasIcon = target && target.assignedNodes().length > 0;
    };

    private _getButtonWidth() {
        if (this._inner) {
            return this._inner.offsetWidth;
        } else return 0;
    }

    private _setSpinnerVariant() {
        switch (this.type) {
            case ButtonType.Primary:
                this._spinnerVariant = 'grey';
                break;
            case ButtonType.TertiaryOnBlack:
                this._spinnerVariant = 'dark-grey';
                break;
            case ButtonType.HighSeverity:
                this._spinnerVariant = 'grey';
                break;
            default:
                this._spinnerVariant = 'light-grey';
        }
    }

    private _handleLoaderStart() {
        this._showLoaderTimerHandle = setTimeout(() => {
            this._loaderShown = true;
        }, 400);
    }

    private _handleLoaderEnd() {
        if (this._showLoaderTimerHandle) {
            clearTimeout(this._showLoaderTimerHandle);
            delete this._showLoaderTimerHandle;
        }
        this._loaderShown = false;
    }

    private _onClick(e: Event) {
        if (this._disabled()) {
            e.stopImmediatePropagation();
        } else if (this.buttonType === 'submit') {
            let form = this.form || this.closest('form');
            if (!form && this.parentElement) {
                const forms = this.parentElement.querySelectorAll('form');
                if (forms.length === 1) {
                    form = forms[0];
                }
            }
            if (form) {
                const fakeSubmit = document.createElement('button');
                fakeSubmit.type = 'submit';
                fakeSubmit.style.display = 'none';
                form.appendChild(fakeSubmit);
                fakeSubmit.click();
                fakeSubmit.remove();
            }
        }
    }

    private _disabled = () => this.disabled || !this.loaded || this._loaderShown;
}
