import { ButtonType } from './buttonType';
import { ButtonSize } from './buttonSize';

export enum ButtonSlot {
    ICON = 'icon-slot',
    LABEL = '',
    NUMBER = 'number-slot',
}

export enum ButtonDesign {
    Default = 'default',
    Notification = 'notification',
    Inline = 'inline-button',
}

export interface ButtonProperties {
    disabled?: boolean;
    fullWidth?: boolean;
    fullHeight?: boolean;
    type?: ButtonType | string;
    compact?: boolean;
    buttonSize?: ButtonSize | string;
    buttonType?: string;
    design?: ButtonDesign | string;
    unseenNotifications?: boolean;
    loaded?: boolean;
    iconAfter?: boolean;

    focus?: (options?: FocusOptions) => void;
    blur?: () => void;
}
